import Promise from 'bluebird';
import bus from '../../bus';
import DriverTemplate from './_base';
import { sessionTimer, backgroundMessaging } from '../../lib';
import { format, log, browserUtils, request, requestTypes } from '../../../utils';

const { toFloat, elemToFloat } = format;
const { wait } = browserUtils;

/* Driver behavior description:
• Existing code: no (was removed because we were getting codes by requests, and we're blocked by merchant)
•	Remove code: yes
•	Stackable: no
•	Free gift support: no
•	‘isValid’ implemented: yes
•	Manually apply code: yes (only the last one)
•	active page: cart, checkout (payment step)
Optional:
•	Show message about free shipping on the cart page: no
•	Is driver SPA and add the condition to show popup: yes
•	Gather codes on site pages: yes
•	Max coupons: 3
•	Session timer: 25 min
Special Case:
• 	codeTesting: once per 15 mins
•	Sometimes merchant blocks applyCode requests. We recieve response with status 403.
	In this case we mark codes as invalid.
•	If there already is code applied on the cart we don't test codes, because there is
	a possibility that we remove code and price will be increased.
*/

const DOMAIN = 'maurices.com';
const DOMAIN_URL = 'maurices.com';

const Driver = class Driver extends DriverTemplate {
	constructor() {
		super(DOMAIN_URL);
		this.PROPOSED_CODES_SELECTOR = '.coupon';
		this.TOTAL_ELEMENT_CONFIG = {
			selector: '.asc-total-value, .order-summary-item-estimated-total .order-summary-content-right',
		};

		this.EXISTING_CODE_SELECTOR = '.applied-promotions .mar-cart-label, #applied-promos';
		this.EXISTING_CODE_REGEX = /\[(\w+)\]/;

		this.SHIPPING_STEP_SELECTOR = '#spa-checkout-shipping.saved';
		this.PAYMENT_STEP_SELECTOR = '#spa-checkout-payment.saved';

		this.CART_EMPTY_SELECTOR = '.asc-empty-cart-message';

		this.DISABLE_TIME_MS = 15 * 60 * 1000; // 15 minutes

		request.setType(requestTypes.embedded);
	}

	async checkCondition() {
		const { href } = window.location;
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 1;
			}
		}

		const totalElem = document.querySelector(this.TOTAL_ELEMENT_CONFIG.selector);
		const totalValue = totalElem ? elemToFloat(totalElem) : false;
		const isOnCheckout = href.includes(`${this.merchantDomain}/${this.merchantName}/checkout`);
		const shippingStepComplete = document.querySelector(this.SHIPPING_STEP_SELECTOR);
		const paymentStepComplete = document.querySelector(this.PAYMENT_STEP_SELECTOR);
		const cartEmptyElem = document.querySelector(this.CART_EMPTY_SELECTOR);
		const isCartEmpty = cartEmptyElem ? cartEmptyElem.style.display === 'block' : false;
		const isAccessDenied = document
			.querySelector('body')
			.textContent.toLowerCase()
			.includes('access denied');

		return (
			isNotMaxCodeTesting &&
			!isAccessDenied &&
			totalValue !== 0 &&
			((this.IS_ON_CART && !isCartEmpty) || (isOnCheckout && shippingStepComplete && !paymentStepComplete))
		);
	}

	beforeTestCodes() {
		const dynSessConf = this.IS_ON_CART
			? document.querySelector(this.SESSION_CONF_SELECTOR).getAttribute('value')
			: Date.now();
		this.applyQueryParams = new URLSearchParams({ pageId: 'UpdateCart' });
		this.applyBodyData = new URLSearchParams({
			Action: 'Cart.validatePromo',
			sessionConfirmationNumber: dynSessConf,
		});
		this.removeQueryParams = new URLSearchParams({ pageId: 'RemoveDiscount' });
		this.removeBodyData = new URLSearchParams({
			Action: 'Cart.removeDiscountAction',
			dArgs: 'dArgs',
			sessionConfirmationNumber: dynSessConf,
		});

		this.isExistingCode = document.querySelector(this.EXISTING_CODE_SELECTOR);

		return this.isExistingCode
			? Promise.resolve()
			: this._getFirstCode().then(code => (code ? this._removeCodesRecursively(code) : Promise.resolve()));
	}

	getMerchantCodes() {
		return [...document.querySelectorAll(this.PROPOSED_CODES_SELECTOR)].map(el => el.textContent).filter(Boolean);
	}

	getExistingCodes() {
		// don't get existing codes, because we are not doing retesting (please add, when merchant doesn't block as)
		return {
			codes: [],
			codeCount: 0,
		};
	}

	removeCodes() {
		return Promise.resolve();
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();
		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 25,
		});
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
		return Promise.resolve();
	}

	checkCodes(codes, originalPrice = this._parseTotal(this.lastResponse)) {
		log('originalPrice: %s', originalPrice);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: false,
			};

			if (this.isExistingCode) {
				return bus.$emit('code-checked', result);
			}

			return this._applyCode(code).then(responseJSON => {
				result.finalPrice = this._parseTotal(responseJSON);
				result.finalDiscount = originalPrice - result.finalPrice;

				if (responseJSON && result.finalDiscount > 0) {
					result.isValid = true;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				}
				return bus.$emit('code-checked', result);
			});
		});
	}

	applyCodes(codes) {
		return this.isExistingCode ? Promise.resolve() : this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		return this._getUpdatedCart().then(json => {
			const total = json.cartSummary.totalPostSvng;
			const shipping = toFloat(json.cartSummary.estmShipping) || 0;
			const tax = json.cartSummary.payment.totalTaxes;
			return total - shipping - tax;
		});
	}

	_applyCode(code, tryNumber = 1) {
		this.applyBodyData.set(this.APPLY_CODE_PARAM, code);
		return Promise.delay(3000).then(() =>
			request
				.post(`${this.MANAGE_CODE_URL}?${this.applyQueryParams.toString()}`, this.applyBodyData, {
					headers: {
						Accept: 'application/json, text/javascript, */*; q=0.01',
						'X-Requested-With': 'XMLHttpRequest',
						'content-type': 'application/x-www-form-urlencoded; charset=UTF-8',
					},
				})
				.then(response => {
					if (response.status === 403 && tryNumber < 2) {
						return this._openIframe().then(() => this._applyCode(code, tryNumber + 1));
					}
					return response.status !== 200 ? null : response.json();
				}),
		);
	}

	async _openIframe() {
		// need to open cart page in iframe to load cookies required for success code apply
		const iframeElem = document.createElement('iframe');
		iframeElem.src = `https://www.${DOMAIN_URL}/maurices/cart/cart.jsp`;
		Object.assign(iframeElem.style, { display: 'none' });
		document.documentElement.appendChild(iframeElem);
		await wait(() => iframeElem.contentDocument.readyState === 'complete', 20000, 5000);
		iframeElem.remove();
		return Promise.resolve();
	}

	_getFirstCode() {
		return this._applyCode('fake-rmn-genie-code').then(
			responseJSON =>
				responseJSON &&
				Boolean(responseJSON.data.cartSummary.savings) &&
				responseJSON.data.cartSummary.savings[0].CouponId,
		);
	}

	// need to remove codes in such way because when we remove one code there the previous one will be on the page
	_removeCodesRecursively(code) {
		this.existingCodes = [code];
		let lastResponse = null;
		let nextCode = code;
		const arr = [...new Array(15)];

		return Promise.each(arr, () =>
			this._removeCode(nextCode).then(responseJSON => {
				lastResponse = responseJSON;
				nextCode =
					Boolean(responseJSON.data.cartSummary.savings) && responseJSON.data.cartSummary.savings[0].CouponId;
				if (nextCode) {
					this.existingCodes.push(nextCode);
					return Promise.resolve();
				}
				return Promise.reject(new Error());
			}),
		).catch(() => {
			this.originalTotal = this._parseTotal(lastResponse);
			return Promise.resolve();
		});
	}

	_getUpdatedCart() {
		return fetch(`https://www.${DOMAIN_URL}/api/v1/cart/summary?_=${Date.now()}`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.json());
	}

	_parseTotal(objJSON) {
		if (objJSON) {
			return toFloat(objJSON.data.cartSummary.totalPostSvng);
		}
		return elemToFloat(document.querySelector(this.TOTAL_ELEMENT_CONFIG.selector));
	}
};

export default {
	name: 'Maurices',
	domain: 'maurices.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 3,
};
