import Promise from 'bluebird';
import bus from '../../bus';
import BaseDriver from '../base';
import { format, log, sendXhr } from '../../../utils';

const { toFloat } = format;

// This template driver is for merchants which are similar to lanebryant.com
// The only parameter is domain

export default class Driver extends BaseDriver {
	constructor(merchantDomain) {
		super();
		this.APPLIED_CODE_REGEX = /\[([^\]]*)/;
		this.REMOVE_CODE_BTN_SELECTOR = '.mar-cart-remove button, .checkout-remove-promo';
		this.APPLIED_CODE_BLOCK_SELECTOR = '.mar-cart-label, .mar-cart-sidebar-pa-text';

		this.IS_ON_CART = window.location.href.includes('/cart/cart.jsp');

		this.APPLY_CODE_PARAM = 'promoCode';
		this.REMOVE_CODE_PARAM = 'discountID';

		this.SESSION_CONF_SELECTOR = 'input[name="_dynSessConf"]';

		this.merchantDomain = merchantDomain;
		this.merchantName = merchantDomain.replace('.com', '');
		this.MANAGE_CODE_URL = `https://www.${this.merchantDomain}/${this.merchantName}/baseAjaxServlet`;

		// this flag true for stackable sites
		this.isStackable = false;
	}

	checkCondition() {
		const { href } = window.location;
		const isRightPage =
			this.IS_ON_CART ||
			href.includes(`${this.merchantDomain}/${this.merchantName}/checkout/checkout.jsp?progressState=PAYMENT`);
		const isCartEmpty = this._isCartEmpty();
		const currentState = isRightPage && !isCartEmpty;
		return currentState;
	}

	watchForCartUrl() {
		super.watchForCartUrl();
	}

	beforeTestCodes() {
		this.applyQueryParams = new URLSearchParams({ pageId: 'UpdateCart' });
		this.applyBodyData = new URLSearchParams({
			Action: 'Cart.validatePromo',
			sessionConfirmationNumber: document.querySelector(this.SESSION_CONF_SELECTOR).getAttribute('value'),
		});
		this.removeQueryParams = new URLSearchParams({ pageId: 'RemoveDiscount' });
		this.removeBodyData = new URLSearchParams({
			Action: 'Cart.removeDiscountAction',
			dArgs: 'dArgs',
			sessionConfirmationNumber: document.querySelector(this.SESSION_CONF_SELECTOR).getAttribute('value'),
		});
		return Promise.resolve();
	}

	getExistingCodes() {
		return this._getCodesFromPage()
			.then(codesFirstPage =>
				codesFirstPage.length ? this._removeAndGetCodes(codesFirstPage) : Promise.resolve(codesFirstPage),
			)
			.then(codes => {
				const finalCodes = codes.filter((value, index, arr) => arr.indexOf(value) === index);
				return Promise.resolve({
					codes: finalCodes,
					codeCount: finalCodes.length,
				});
			});
	}

	beforeCheckCodes() {
		// if codes haven't been found this request is sent. Before renewing template, there was a problem with non-actual total on the page.
		// This request solves problem that possibly exist.
		if (!this.lastResponse) {
			return this._applyCode('fake-rmn-genie-code').then(responseJSON => this._parseTotal(responseJSON));
		}
		return Promise.resolve();
	}

	checkCodes(codes, originalPrice = this._parseTotal(this.lastResponse)) {
		log('originalPrice: %s', originalPrice);
		const workingDiscounts = [];
		return Promise.each(codes, code =>
			this._applyCode(code).then(responseJSON => {
				const finalPrice = this._parseTotal(responseJSON);
				const finalDiscount = originalPrice - finalPrice;
				const result = {
					code,
					finalPrice,
					finalDiscount,
					isValid: false,
				};
				if (finalDiscount > 0) {
					result.isValid = true;
					if (this.isStackable) {
						workingDiscounts.push(result);
					}
				}
				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			}),
		).then(() =>
			this.isStackable
				? bus.$emit('stack-codes', {
						codes: workingDiscounts.map(discount => discount.code),
						discounts: workingDiscounts,
						originalPrice,
				  })
				: Promise.resolve(),
		);
	}

	stackCodes(validationInfo) {
		const { discounts, originalPrice } = validationInfo;
		discounts.sort((a, b) => b.finalDiscount - a.finalDiscount);
		let currentPrice = originalPrice;
		let currentDiscount = 0;
		const successfulCodes = [];
		return Promise.each(discounts, discount =>
			this._applyCode(discount.code).then(responseJSON => {
				const finalPrice = this._parseTotal(responseJSON);
				const finalDiscount = originalPrice - finalPrice;
				bus.$emit('code-stacked', {
					code: discount.code,
					finalPrice,
					finalDiscount,
				});
				if (finalPrice < currentPrice) {
					successfulCodes.push(discount.code);
					currentDiscount = finalDiscount;
					currentPrice = finalPrice;
					return Promise.resolve();
				}
				return this._removeCode(discount.code);
			}),
		).then(() =>
			bus.$emit('stackable-complete', {
				codes: successfulCodes,
				finalPrice: currentPrice,
				finalDiscount: currentDiscount,
			}),
		);
	}

	applyCodes(codes) {
		window.history.replaceState(window.history.state, window.document.title, window.location.href.split('?')[0]);
		return this.isStackable ? Promise.resolve() : this._applyCode(codes[0]);
	}

	_removeAndGetCodes(previousPagesCodes) {
		return this._removeCode(previousPagesCodes[previousPagesCodes.length - 1]).then(responseJSON => {
			this.lastResponse = responseJSON;
			const currentPageCodes = this._getCodesFromResponse(responseJSON);
			const currentCodes = previousPagesCodes.concat(currentPageCodes);
			return currentPageCodes.length ? this._removeAndGetCodes(currentCodes) : Promise.resolve(currentCodes);
		});
	}

	_applyCode(code) {
		this.applyBodyData.set(this.APPLY_CODE_PARAM, code);
		return this._manageCode(this.applyQueryParams, this.applyBodyData);
	}

	_removeCode(code) {
		this.removeBodyData.set(this.REMOVE_CODE_PARAM, code);
		return this._manageCode(this.removeQueryParams, this.removeBodyData);
	}

	_manageCode(queryParams, bodyData) {
		return sendXhr(`${this.MANAGE_CODE_URL}?${queryParams.toString()}`, {
			method: 'POST',
			credentials: 'include',
			body: bodyData,
			headers: {
				Accept: 'application/json, text/javascript, */*; q=0.01',
				'X-Requested-With': 'XMLHttpRequest',
			},
		}).then(response => response.json());
	}

	_parseTotal(objJSON) {
		return toFloat(objJSON.data.cartSummary.totalPostSvng);
	}

	_getCodesFromResponse(data) {
		const codes = data.data.cartSummary.savings ? data.data.cartSummary.savings : [];
		return codes.map(element => element.CouponId).filter(Boolean);
	}

	_getCodesFromPage() {
		const codes = [...document.querySelectorAll(this.REMOVE_CODE_BTN_SELECTOR)]
			.map(element => element.closest(this.APPLIED_CODE_BLOCK_SELECTOR).innerText.match(this.APPLIED_CODE_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
		return Promise.resolve(codes);
	}

	_isCartEmpty() {
		return false;
	}
}
