import Promise from 'bluebird';
import DriverTemplate from './_base';
import { sessionTimer } from '../../lib';
import { format, DOM } from '../../../utils';

const { toFloat } = format;
const { extractElement } = DOM;

/*
Driver behavior description:
- Existing code: yes
- Remove code: yes
- Stackable: no
- Free gift support: no
- Free shipping: no
- ‘isValid’ implemented: yes
- Is SPA: yes
- Codes can be replaced: no
- Pages, where extension works:
  - cart
  - checkout (payment)
- Gather codes on site pages: yes(cart page)
- Session timer: 15 min
- Special case about the driver works:
When a lot of codes were applied and one removed, the previous one is applied automatically.
to get all applied codes we have to loop through fetch requests and stop when no applied codes
*/

const DOMAIN_NAME = 'catherines';
const DOMAIN_URL = `${DOMAIN_NAME}.com`;

const Driver = class Driver extends DriverTemplate {
	constructor() {
		super(DOMAIN_URL);

		// this flag true for stackable sites
		this.isStackable = false;

		this.PROMO_INPUT_SELECTOR = '#mar-cart-promo-code, .promo-code-input';
		this.MANAGE_CODE_URL = `https://${window.location.host}/${this.merchantName}/baseAjaxServlet`;

		this.SHIPPING_STEP_SELECTOR = '#spa-checkout-shipping';

		this.EMPTY_CART_SELECTOR =
			'.items-in-order-wrapper[style*="display: none"], #asc-cart-order-summary[style*="display: none"]';
		this.MERCHANT_CODE_SELECTOR = '.desktop-code b';
		sessionTimer.initialize();
	}

	checkCondition() {
		const onCheckout = window.location.href.includes('/checkout');
		const shippingStep = document.querySelector(this.SHIPPING_STEP_SELECTOR);
		const notOnShippingStep = shippingStep ? shippingStep.classList.contains('saved') : false;
		const couponInput = document.querySelector(this.PROMO_INPUT_SELECTOR);
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		return couponInput && ((onCheckout && notOnShippingStep) || (this.IS_ON_CART && !emptyCart));
	}

	beforeTestCodes() {
		const dynSessConf = this.IS_ON_CART
			? document.querySelector(this.SESSION_CONF_SELECTOR).getAttribute('value')
			: Date.now();
		this.applyQueryParams = new URLSearchParams({ pageId: 'UpdateCart' });
		this.applyBodyData = new URLSearchParams({
			Action: 'Cart.validatePromo',
			sessionConfirmationNumber: dynSessConf,
		});
		this.removeQueryParams = new URLSearchParams({ pageId: 'RemoveDiscount' });
		this.removeBodyData = new URLSearchParams({
			Action: 'Cart.removeDiscountAction',
			dArgs: 'dArgs',
			sessionConfirmationNumber: dynSessConf,
		});

		return this._getFirstCode().then(code => (code ? this._removeCodesRecursively(code) : Promise.resolve()));
	}

	getMerchantCodes() {
		return fetch(`https://www.${DOMAIN_URL}/catherines/cart/cart.jsp`, {
			method: 'GET',
			credentials: 'include',
		})
			.then(res => res.text())
			.then(resHtml => {
				return [extractElement(this.MERCHANT_CODE_SELECTOR, resHtml)].filter(Boolean).map(el => el.textContent);
			});
	}

	getExistingCodes() {
		const codes = this.existingCodes ? this.existingCodes : [];
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes() {
		return this.originalTotal;
	}

	async getPreTaxShippingTotal() {
		const cartJSON = await this._getUpdatedCart();
		const total = cartJSON.cartSummary.merchandiseAfterDiscount;

		return toFloat(total);
	}

	_getFirstCode() {
		return this._applyCode('fake-code').then(
			responseJSON =>
				Boolean(responseJSON.data.cartSummary.savings) && responseJSON.data.cartSummary.savings[0].CouponId,
		);
	}

	// need to remove codes in such way because when we remove one code there the previous one will be on the page
	_removeCodesRecursively(code) {
		this.existingCodes = [code];
		let lastResponse = null;
		let nextCode = code;
		const arr = [...new Array(15)];

		return Promise.each(arr, () => {
			if (!nextCode) {
				this.originalTotal = this._parseTotal(lastResponse);
				return Promise.resolve();
			}
			return this._removeCode(nextCode).then(responseJSON => {
				lastResponse = responseJSON;
				nextCode =
					Boolean(responseJSON.data.cartSummary.savingsNew) &&
					responseJSON.data.cartSummary.savingsNew[0] &&
					responseJSON.data.cartSummary.savingsNew[0].promoCode &&
					this.existingCodes[this.existingCodes.length - 1] !==
						responseJSON.data.cartSummary.savingsNew[0].promoCode
						? responseJSON.data.cartSummary.savingsNew[0].promoCode
						: false;
				if (nextCode) {
					this.existingCodes.push(nextCode);
					return Promise.resolve();
				}
				return Promise.resolve();
			});
		});
	}

	_manageCode(queryParams, bodyData) {
		return this._mouseEvent('mousedown')
			.then(() => {
				return fetch(`${this.MANAGE_CODE_URL}?${queryParams.toString()}`, {
					method: 'POST',
					credentials: 'include',
					body: bodyData,
				});
			})
			.then(res => {
				return this._mouseEvent('mouseup').then(() => {
					return res.json();
				});
			})
			.then(responseJSON => {
				this.lastResponse = responseJSON;
				return responseJSON;
			});
	}

	_mouseEvent(event) {
		const codeInput = document.querySelector(this.PROMO_INPUT_SELECTOR);
		codeInput.dispatchEvent(new Event(event, { bubbles: true }));
		// need to add this delay for correct site working
		return Promise.delay(1000).then(() => {
			return Promise.resolve();
		});
	}

	_getUpdatedCart() {
		return fetch(`https://www.${DOMAIN_URL}/api/v1/cart/summary?_=${Date.now()}`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.json());
	}
};

export default {
	name: 'Catherines',
	domain: 'catherines.com',
	Driver,
	isSinglePageApp: true,
};
