import Promise from 'bluebird';
import DriverTemplate from './_base';
import BaseTemplate from '../base';
import bus from '../../bus';
import { log, format } from '../../../utils';
import { sessionTimer } from '../../lib';

const { toFloat, elemToFloat } = format;

/** Behavior:
+ Existing code: yes
+ Remove code: yes
+ Stackable:  no
+ Free gift support:  no
+ Free shipping:  no
+ ‘isValid’ implemented: yes
+ Manually apply code: yes (checkout)
+ Pages, where extension works: cart, checkout
+ Is driver SPA yes
+ Gather codes on site pages:  no
+ Special case about the driver works (if it needed).
   - sessionTimer yes (15min)
 * * */

const DOMAIN_NAME = 'justice';
const DOMAIN_URL = 'shopjustice.com';
const isOnCart = () => window.location.href.includes('/cart/cart.jsp');

const DriverCart = class Driver extends DriverTemplate {
	constructor() {
		super(DOMAIN_URL);
		this.MANAGE_CODE_URL = `https://www.${DOMAIN_URL}/${DOMAIN_NAME}/baseAjaxServlet`;
		this.INPUT_SELECTOR = '#mar-cart-promo-code';
		this.EMPTY_SELECTOR = '#asc-cart-order-summary[style*="display: none"]';
		this.TOTAL_SELECTOR = '.asc-total-value';
		this.SHIPPING_SELECTOR = '.mar-order-summary-shipping-charge span';
		this.TAX_SELECTOR = '.asc-tax-value';
		this.LOADER_SELECTOR = '.asc-global-indicator.show';

		// this flag true for stackable sites
		this.isStackable = false;
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const isOnHref = window.location.href.includes(`${DOMAIN_URL}/justice/cart/cart.jsp`);
		const isEmpty = document.querySelector(this.EMPTY_SELECTOR);
		const isTotal = (document.querySelector(this.TOTAL_SELECTOR) || {}).offsetParent;
		const inputElement = document.querySelector(this.INPUT_SELECTOR);
		const isInput = inputElement && inputElement.offsetParent;
		const isLoader = document.querySelector(this.LOADER_SELECTOR);
		const currentState = isOnHref && !isEmpty && isTotal && isInput && !isLoader;
		return currentState;
	}

	_getCodesFromPage() {
		return this._applyCode('fake-code').then(responseJSON => this._getCodesFromResponse(responseJSON));
	}

	applyCodes(codes) {
		window.history.replaceState(window.history.state, window.document.title, window.location.href.split('?')[0]);
		return this._applyCode(codes[0]);
	}

	async getPreTaxShippingTotal() {
		let shipping;
		let total;
		let tax;
		const APPLY_RESPONSE = await this._applyCode('fake-code');
		if (APPLY_RESPONSE) {
			total = this._parseTotal(APPLY_RESPONSE);
			shipping = toFloat(
				APPLY_RESPONSE.data.cartSummary.estmShipping.toLowerCase() === 'free'
					? 0
					: APPLY_RESPONSE.data.cartSummary.estmShipping,
			);
			tax = toFloat(APPLY_RESPONSE.data.cartSummary.payment.totalTaxes);
		} else {
			total =
				document.querySelector(this.TOTAL_SELECTOR) && elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
			const shippingEl = document.querySelector(this.SHIPPING_SELECTOR);
			shipping =
				shippingEl.textContent.toLowerCase() === 'free'
					? 0
					: elemToFloat(document.querySelector(this.SHIPPING_SELECTOR));
			tax = document.querySelector(this.TAX_SELECTOR) && elemToFloat(document.querySelector(this.TAX_SELECTOR));
		}

		return total - shipping - tax;
	}
};

const DriverCheckout = class Driver extends BaseTemplate {
	constructor() {
		super();
		this.API_URL = `https://www.${DOMAIN_URL}/api/v1`;
		this.INPUT_SELECTOR = '[name="promo-code"]';
		this.APPLIED_CODES_SELECTOR = '.promo-item-remove';
		this.PAYMENT_SELECTOR = '.payment-container';
		this.TOTAL_SELECTOR = '.order-summary-item-estimated-total .order-summary-content-right';
		this.EMPTY_SELECTOR = '.sidebar-group-header-count';
	}

	watchForCartUrl() {
		const interval = super.watchForCartUrl();

		sessionTimer.initialize({
			actionInterval: interval,
			timeout: 15,
		});
	}

	checkCondition() {
		const isOnHref = window.location.href.includes(`${DOMAIN_URL}/justice/checkout`);
		const isEmpty = document.querySelector(this.EMPTY_SELECTOR)
			? document.querySelector(this.EMPTY_SELECTOR).innerText === '0 item'
			: false;
		const isTotal = document.querySelector(this.TOTAL_SELECTOR);
		const isInput = document.querySelector(this.INPUT_SELECTOR);
		const isPayment = document.querySelector(this.PAYMENT_SELECTOR);
		const isActivePayment = isPayment ? isPayment.style.overflow : false;
		const currentState = isOnHref && !isEmpty && isTotal && isInput && !isActivePayment;
		return currentState;
	}

	beforeTestCodes() {
		return this._getSessionNumber().then(responseJSON => {
			this.applyBodyData = {
				_dynSessConf: responseJSON.Progress.checkoutState.sessionConfirmationNumber,
			};
			this.removeBodyData = this.applyBodyData;
		});
	}

	getExistingCodes() {
		const codes = [...document.querySelectorAll(this.APPLIED_CODES_SELECTOR)].map(
			codeElement => codeElement.dataset.code,
		);
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0])
			.then(() => this._getOrderInfo())
			.then(responseJSON => this._parseTotal(responseJSON));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log('originalPrice: %s', originalPrice);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
				isValid: false,
			};
			return this._applyCode(code).then(responseJSON => {
				if (responseJSON.status === 'success') {
					return this._getOrderInfo().then(responseCartJSON => {
						result.finalPrice = this._parseTotal(responseCartJSON);
						result.finalDiscount = originalPrice - result.finalPrice;
						result.isValid = true;
						this._removeCode(code).then(() => bus.$emit('code-checked', result));
					});
				}
				return bus.$emit('code-checked', result);
			});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	async getPreTaxShippingTotal() {
		const checkoutData = await this._getOrderInfo();
		const total = this._parseTotal(checkoutData);

		const shipping = toFloat(
			checkoutData.cartSummary.estmShipping.toLowerCase() === 'free' ? 0 : checkoutData.cartSummary.estmShipping,
		);
		const tax = toFloat(checkoutData.cartSummary.payment.totalTaxes);
		return total - shipping - tax;
	}

	_applyCode(code) {
		this.applyBodyData.promoCode = code;
		return this._manageCode(this.applyBodyData, 'add');
	}

	_removeCode(code) {
		this.removeBodyData.discountID = code;
		return this._manageCode(this.removeBodyData, 'remove');
	}

	_manageCode(bodyData, action) {
		return fetch(`${this.API_URL}/order/promo/${action}`, {
			method: 'POST',
			credentials: 'include',
			body: JSON.stringify(bodyData),
			headers: {
				'content-type': 'application/json; charset=UTF-8',
				'X-Requested-With': 'XMLHttpRequest',
				clientid: 'asc_web',
			},
		}).then(response => response.json());
	}

	_getSessionNumber() {
		return fetch(`${this.API_URL}/order/checkout/state`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.json());
	}

	_getOrderInfo() {
		return fetch(`${this.API_URL}/cart/summary`, {
			method: 'GET',
			credentials: 'include',
			headers: {
				clientid: 'asc_web',
				'X-Requested-With': 'XMLHttpRequest',
			},
		}).then(response => response.json());
	}

	_parseTotal(objJSON) {
		if (objJSON) {
			return toFloat(objJSON.cartSummary.totalPostSvng);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
};

export default {
	name: 'Shop Justice',
	domain: 'shopjustice.com',
	Driver: isOnCart() ? DriverCart : DriverCheckout,
	isSinglePageApp: true,
};
