import Promise from 'bluebird';
import bus from '../../bus';
import DriverTemplate from './_base';
import { log, format, DOM } from '../../../utils';
import { sessionTimer, handleDriverError } from '../../lib';

const { toFloat } = format;
const { extractElement, extractData } = DOM;

/* Driver behavior description:
	+ Existing code: yes (some types of applied codes)
	+ Remove code: yes
	+ Stackable: no
	+ Free gift support: no
	+ Free shipping: no
	+ ‘isValid’ implemented: yes
	+ Is SPA: yes
	+ Gather codes: yes, from https://www.lanebryant.com/content/promos , https://outlet.lanebryant.com/content/promos (according to the host)
	+ Extension works: Cart, checkout (lanebryant, outlet)
	+ SessionTimer: 13min
====Special cases====
	+ When a lot of codes were applied and one removed, the previous one will be applied automatically.
	+ Some codes which were previous applied can be present in script even after remove them,
	  so the will be present in existing codes too
*/

const DOMAIN_URL = 'lanebryant.com';
const Driver = class Driver extends DriverTemplate {
	constructor() {
		super(DOMAIN_URL);
		this.API_URL = `https://${window.location.host}/api/v1/cart/summary`;
		this.CART_URL = `https://${window.location.host}/cart/cart.jsp`;
		this.PROMO_CODES_URL = `https://${window.location.host}/content/promos`;
		this.CHECKOUT_URL = `https://${window.location.host}/lanebryant/checkout`;
		this.CHECKOUT_API_URL = `https://${window.location.host}/api/v1/order`;
		this.CHECKOUT_STATE_URL = `${this.CHECKOUT_API_URL}/checkout/state`;
		this.CHECKOUT_MANAGE_CODE_URL = `${this.CHECKOUT_API_URL}/promo/`;
		this.TOTAL_ELEMENT_CONFIG = {
			selector: '.order-summary-item-estimated-total .order-summary-content-right, .asc-total-value',
		};
		this.EMPTY_CART_SELECTOR = '.asc-empty-cart-message';
		this.APPLIED_CODE_BLOCK_SELECTOR = '.promo-left-content, .mar-cart-label, .mar-cart-sidebar-pa-text';
		this.PAYMENT_CONT_SELECTOR = '.payment-container';
		this.EMPTY_EL_SELECTOR =
			'.items-in-order-wrapper[style*="display: none"], #asc-cart-order-summary[style*="display: none"]';
		this.ITEM_SELECTOR = '.item-title, .product-title';
		this.SESSION_CONF_SELECTOR = 'input[name="_dynSessConf"]';
		this.MERCHANT_CODE_SELECTOR = '#globo-box h3 small, .promo-ftr';
		this.CODE_CHECKOUT_SELECTOR = '[data-code]';
		this.MERCHANT_CODE_REGEX = /use code: (\w+)/i;
		this.SPECIFIC_APPLIED_CODE_REGEX = /with code:?\s?(\w+)/i;
		this.SCRIPT_CODES_REGEX = /promotionVanityCode"?:\s?"?(\w+)/i;
		sessionTimer.initialize({ timeout: 13 });
	}

	checkCondition() {
		const isCartEmpty = document.querySelector(this.EMPTY_EL_SELECTOR);
		const paymentElOnCheckout = document.querySelector(this.PAYMENT_CONT_SELECTOR);
		const isPaymentHide = document.querySelector(`${this.PAYMENT_CONT_SELECTOR}.slide-hide`);
		const isPayment = paymentElOnCheckout && paymentElOnCheckout.style.overflow !== 'hidden' && !isPaymentHide;
		const totalEl = (document.querySelector(this.TOTAL_ELEMENT_CONFIG.selector) || {}).offsetParent;
		const itemCheckout = document.querySelector(this.ITEM_SELECTOR);
		const isOnCart = window.location.href.includes('cart.jsp');
		const isOnCaciquePage = window.location.href.includes('cacique');
		return (isOnCart || isPayment) && !isCartEmpty && itemCheckout && totalEl && !isOnCaciquePage;
	}

	async beforeTestCodes() {
		const resHtml = await this._getUpdatedCart();
		const dynSessConfEl = extractElement(this.SESSION_CONF_SELECTOR, resHtml);
		let _dynSessConf = dynSessConfEl ? extractData(this.SESSION_CONF_SELECTOR, resHtml, false, 'value') : null;
		if (!_dynSessConf) {
			const { Progress } = await this._getOrderSummary(`${this.CHECKOUT_STATE_URL}?_=${Date.now()}`);
			_dynSessConf = Progress.checkoutState.sessionConfirmationNumber;
		}
		this.addCodeParams = new URLSearchParams({ _dynSessConf });
		this.removeCodeParam = new URLSearchParams({ _dynSessConf });
	}

	getMerchantCodes() {
		return fetch(this.PROMO_CODES_URL, {
			method: 'GET',
			credentials: 'include',
		})
			.then(res => res.text())
			.then(resHtml => [
				...new Set(
					[...extractElement(this.MERCHANT_CODE_SELECTOR, resHtml, true)]
						.filter(el => el.innerHTML.match(this.MERCHANT_CODE_REGEX))
						.map(el => el.innerHTML.match(this.MERCHANT_CODE_REGEX)[1]),
				),
			]);
	}

	getExistingCodes() {
		let checkoutCodes = [];
		if (window.location.href.includes('checkout')) {
			checkoutCodes = [...document.querySelectorAll(this.CODE_CHECKOUT_SELECTOR)]
				.map(el => el.dataset.code)
				.filter(Boolean);
		}
		const pageCodes = [...document.querySelectorAll(this.APPLIED_CODE_BLOCK_SELECTOR)]
			.map(
				el =>
					el.innerText.match(this.APPLIED_CODE_REGEX) || el.innerText.match(this.SPECIFIC_APPLIED_CODE_REGEX),
			)
			.filter(Boolean)
			.map(el => el[1]);
		const codes = [...new Set([...pageCodes, ...this._parseCodesFromScript(), ...checkoutCodes])];
		return {
			codes,
			codeCount: codes.length,
		};
	}

	removeCodes(codes) {
		return this._removeCode(codes[0]);
	}

	beforeCheckCodes() {
		return this._parseTotal();
	}

	checkCodes(codes, originalPrice) {
		log('originalPrice: %s', originalPrice);
		return Promise.each(codes, code =>
			this._applyCode(code).then(async response => {
				const result = {
					code,
					finalPrice: originalPrice,
					finalDiscount: 0,
					isValid: true,
				};
				if (!response || response.error) {
					result.isValid = false;
					return bus.$emit('code-checked', result);
				}
				result.finalPrice = await this._parseTotal();
				result.finalDiscount = originalPrice - result.finalPrice;
				return this._removeCode(code).then(() => bus.$emit('code-checked', result));
			}),
		);
	}

	getPreTaxShippingTotal(retry = false) {
		return this._getOrderSummary().then(json => {
			if (json.status !== 'success' && !retry) {
				return this.getPreTaxShippingTotal(true);
			}
			const { totalPostSvng: discountTotal, estmShipping, payment } = json.cartSummary;
			const shipping = estmShipping !== 'FREE' ? estmShipping : 0;
			if (!payment) {
				handleDriverError(new Error(`Cannot read property 'totalTaxes' of undefined`), json.cartSummary);
				return null;
			}
			const tax = payment.totalTaxes;
			return discountTotal - shipping - tax;
		});
	}

	_getUpdatedCart() {
		return fetch(this.CART_URL, {
			method: 'GET',
		}).then(res => res.text());
	}

	_applyCode(code) {
		this.addCodeParams.set('promoCode', code);
		return this._manageCode(this.addCodeParams, 'add');
	}

	_removeCode(code) {
		this.removeCodeParam.set('discountID', code);
		return this._manageCode(this.removeCodeParam, 'remove');
	}

	_manageCode(urlParams, action) {
		return fetch(`${this.CHECKOUT_MANAGE_CODE_URL}${action}`, {
			method: 'POST',
			credentials: 'include',
			body: urlParams,
			headers: {
				Accept: 'application/json, text/javascript, */*; q=0.01',
				'X-Requested-With': 'XMLHttpRequest',
			},
		}).then(response => (response.status === 409 ? null : response.json()));
	}

	_parseCodesFromScript() {
		return [...document.querySelectorAll('script')]
			.map(el => el.innerText.match(this.SCRIPT_CODES_REGEX))
			.filter(Boolean)
			.map(el => el[1]);
	}

	_getOrderSummary(url) {
		return fetch(url || this.API_URL, {
			method: 'GET',
			credentials: 'include',
		}).then(res => res.json());
	}

	_parseTotal(retry = false) {
		return this._getOrderSummary().then(json => {
			if (json.status !== 'success' && !retry) {
				return this._parseTotal(true);
			}
			return toFloat(json.cartSummary.totalPostSvng);
		});
	}
};

export default {
	name: 'Lane Bryant',
	domain: 'lanebryant.com',
	Driver,
	isSinglePageApp: true,
};
